@charset "UTF-8";
body {
  font-family: "Noto Serif JP", serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .fv {
    height: 100vh;
  }
}

.fv__inner {
  position: relative;
  height: 100%;
}

.fv__img {
  position: relative;
}
@media screen and (min-width: 768px) {
  .fv__img {
    height: 100%;
  }
}
.fv__img::after {
  content: "";
  width: 100%; /* 幅いっぱい */
  height: 100%; /* 高さいっぱい */
  display: block; /* 高さを指定するためにブロック要素にする */
  background: linear-gradient(rgba(255, 255, 255, 0) 80%, #fff 100%); /* 徐々に透明にする */
  position: absolute; /*  */
  top: 0;
  left: 0;
}

.fv__img img {
  height: 100%;
  object-fit: cover;
}

.fv__title-wrap {
  text-align: center;
  padding-left: 0.625rem;
  padding-right: 0.625rem;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .fv__title-wrap {
    position: absolute;
    max-width: 35.625rem;
    top: 7.1875rem;
    left: initial;
    right: 5rem;
    padding-left: initial;
    padding-right: initial;
    transform: initial;
  }
}

.fv__title {
  font-size: 2rem;
  font-weight: 900;
}
@media screen and (min-width: 768px) {
  .fv__title {
    font-size: 3rem;
  }
}

.fv__sub-title {
  margin-top: 0.625rem;
  font-size: 1.25rem;
}
@media screen and (min-width: 768px) {
  .fv__sub-title {
    margin-top: 1.25rem;
    font-size: 3rem;
  }
}

.fv__btn-wrap {
  margin-top: 1.875rem;
  max-width: 18.4375rem;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media screen and (min-width: 768px) {
  .fv__btn-wrap {
    margin-top: 2.5rem;
    max-width: 35.625rem;
  }
}

.fv__btn {
  width: 100%;
}
.fv__btn:first-child {
  max-width: 8.125rem;
}
@media screen and (min-width: 768px) {
  .fv__btn:first-child {
    max-width: 18.9375rem;
  }
}
.fv__btn:last-child {
  max-width: 8.4375rem;
}
@media screen and (min-width: 768px) {
  .fv__btn:last-child {
    max-width: 14.1875rem;
  }
}

.fv__cta {
  width: 100%;
  height: 6.6875rem;
  background: linear-gradient(90deg, rgb(0, 183, 255) 0%, rgb(255, 223, 199) 100%);
  margin-top: 1.5625rem;
}
@media screen and (min-width: 768px) {
  .fv__cta {
    margin-top: initial;
    position: absolute;
    bottom: 0;
    left: 0;
    height: 13.5rem;
    background: linear-gradient(90deg, rgba(0, 183, 255, 0.6) 0%, rgba(255, 223, 199, 0.6) 100%);
    backdrop-filter: blur(2.5rem);
  }
}

.fv__cta-btn {
  width: 100%;
  height: 100%;
  font-size: 1.5rem;
  font-weight: 900;
  color: #fff;
  display: flex;
  justify-content: center;
  gap: 0.9375rem;
  align-items: center;
  position: relative;
  padding-left: 3.4375rem;
}
@media screen and (min-width: 768px) {
  .fv__cta-btn {
    font-size: 3rem;
    gap: 2.75rem;
  }
}
.fv__cta-btn span {
  width: 2.5rem;
  height: 2.5rem;
}
@media screen and (min-width: 768px) {
  .fv__cta-btn span {
    width: 4.375rem;
    height: 4.375rem;
  }
}